body {
  margin: 0;
  padding: 15px;
}

#header a, #qv-form-input-intro a {
  color: #337ab7;
}

#header {
  color: #202020;
  border-top: 2px solid #0bacb6;
  border-bottom: 2px solid #17b4e3;
  padding-bottom: 10px;
  font-family: Droid Sans, Arial, Helvetica, sans-serif;
  line-height: normal;
  display: none;
  position: relative;
}

#siteNameBox {
  margin-top: 15px;
  margin-bottom: 5px;
