#lformsVersionMenu {
  display: none;
}

#qv-form-input-intro {
  margin: 5px;
}

#qv-form-input-settings {
  border: 1px solid #17b4e3;
  border-radius: 4px;
  flex-direction: column;
  margin: 5px;
  display: flex;
}

#qv-form-input-settings .qv-url-row {
  flex-direction: row;
  margin: 6px 40px 6px 10px;
  display: flex;
}

#qv-form-input-settings .qv-url-name {
  width: 30rem;
  margin-top: 4px;
  font-size: 16px;
}

#qv-form-input-settings .qv-url-name label {
  margin-left: .25em;
  font-weight: 400;
}

#qv-form-input-settings .qv-url-value {
  flex-grow: 1;
  flex-shrink: 1;
}

#qv-form-input-settings .qv-url-value input {
  border: 2px solid #17b4e3;
  border-radius: 4px;
  width: 100%;
  margin-left: .5em;
  padding-left: .25em;
}

#qv-form-input-settings .qv-url-value input:disabled {
  background: #ddd;
  border-color: gray;
