#qv-param-notes {
  margin: 10px;
  font-size: 80%;
}

#qv-btn-load {
  border: 2px solid #17b4e3;
  border-radius: 4px;
  margin: 10px 5px;
  font-size: 125%;
}

#qv-error {
  background-color: #ffffe0;
  border: 2px solid red;
  margin-top: .5em;
  padding-top: 1em;
  padding-left: 1em;
  padding-right: 1em;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 14px;
}

#qv-error-header {
  color: red;
  font-size: 120%;
}

#qv-error-message {
  font-size: 110%;
